
.home {
    background-image: url('https://bhubaneswarlive.com/wp-content/uploads/2016/11/Savin-NY-Website-Background-Web.jpg');
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center
}
.well {
    background: rgb(127, 127, 127)
}
.navbar {
    background: black;
    font: white;
}

/* .parallax {
    The image used
    background-image: url("https://images.pexels.com/photos/34092/pexels-photo.jpg?w=1260&h=750&dpr=2&auto=compress&cs=tinysrgb");

    Set a specific height
    height: 500px;

    Create the parallax scrolling effect
    background-attachment: fixed;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
} */
.main-pic {
    background-image: url('https://images.pexels.com/photos/34092/pexels-photo.jpg?w=1260&h=750&dpr=2&auto=compress&cs=tinysrgb');
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
}
.main-heading {
    font-family: 'Abril Fatface', cursive;
    text-shadow: 0px 0px 15px black;
    color: white;
}
.second-pic {
    /* margin-top: 300px; */
    height: 300px;
    background-image: url('https://i.pinimg.com/originals/50/1f/1a/501f1a19290e28cd676605f9acf36d03.jpg');
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
}
.info-text {
    font-family: 'Source Sans Pro', sans-serif;
}
.btn-default {
    width: 100%;
}
.screenshot {
    margin-left: 50px;
}
.navbar ul {
    list-style-type: none;
}
.logout-btn {
    margin-left:2px;
}
@media (max-width: 2000px) {
.navbar-header {
        float: none;
}
.navbar-toggle {
        display: block;
}
.navbar-collapse {
        border-top: 1px solid transparent;
        box-shadow: inset 0 1px 0 rgba(255, 255, 255, 0.1);
}
.navbar-collapse.collapse {
        display: none!important;
}
.navbar-collapse.collapse.in {
        display: block!important;
}
.navbar-nav {
        float: none!important;
        margin: 7.5px -15px;
}
.navbar-nav>li {
        float: none;
}
.navbar-nav>li>a {
        padding-top: 10px;
        padding-bottom: 10px;
}
.state {
        width: 50%
}
.nav-drop-btn {
        width: 90%;
        background-color: #FFFFF9;
        margin: 2px;
        font: black;
}
}



